body {
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
body {
  margin: 0;
  padding: 0;
}

.section {
  padding: 60px 0;
}
.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
}
.content-box {
  border: 1px solid #eaeaea;
  border-radius: 20px;
  padding: 26px;
}
.content-heading {
  font-weight: 700;
  font-size: 24px;
  margin: 0 0 10px 0;
}
.content-desc {
  font-size: 15px;
  margin: 0;
}
.content-wrapper {
  display: flex;
  flex-direction: column;
  gap: 10px;
}
.good-btn {
  cursor: pointer;
}
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.4);
  display: flex;
  justify-content: center;
  align-items: center;
}
.modal {
  width: 400px;
  height: 300px;
  background-color: #eaeaea;
  border-radius: 20px;
  padding: 30px;
}
